.contact-us-section {
    width: 100%;
    height: 100%;
    background-color: #fff;
}

.contact-container {
    display: flex;
    flex-direction: row;
    gap: 12px;
    margin-top: 50px;
    margin-bottom: 50px;
}
@media all and (max-width: 767px) {
    .contact-container {
        flex-direction: column
    }
}